import { Dialog } from "@mui/material";
import React, { useEffect, useState } from "react";
import style from "./CollectModal.module.css";
import { useTranslation } from "react-i18next";
import Switch from "@mui/material/Switch";
import { TextInput } from "../../../../../../../Authentication/Auth_New_Flow/InputWithoutIcon/InputWithoutIcon";
import { imageShowUrl2 } from "../../../../../../../services/BaseUrl";
import SelectOptions from "../CollectModal/SelectOptions";
import { useCollectBook } from "../../../../../../../hooks/member_tab/library-api";
import LoaderButton from "../../../../../../../Loader/LoaderButton";
import QLoader from "../../../../../../../Loader/QLoader";

const label = { inputProps: { "aria-label": "Switch demo" } };

function CollectModal({ open, hideModal, issueId, onRefetchWhenBookCollect }) {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [isOpen, setIsOpen] = useState(false);
  const [collectBook] = useCollectBook();
  const [chargeType, setChargeType] = useState({
    damaged: false,
    lost: false,
  });
  const [collectData, setCollectData] = useState({
    enterAmount: false,
    fineCharge: 0,
    paymentType: "Offline",
  });

  const onChargeBy = (val) => {
    if (val.name === "Damaged") {
      setChargeType((prevState) => ({
        ...prevState,
        damaged: val.value,
        lost: false,
      }));
    } else {
      setChargeType((prevState) => ({
        ...prevState,
        damaged: false,
        lost: val.value,
      }));
    }
  };

  const handleCollect = () => {
    if (collectData.fineCharge === 0) {
      setCollectData((prev) => ({
        ...prev,
        enterAmount: true,
      }));
    } else {
      setDisabled((pre) => !pre);
      collectBook({
        lid: issueId,
        collectData: {
          chargeBy: chargeType.damaged
            ? "Damaged"
            : chargeType.lost
            ? "Lost"
            : "",
          paymentType: collectData.paymentType,
          fineCharge: collectData.fineCharge,
        },
      })
        .then(() => {
          onRefetchWhenBookCollect();
          hideModal(false);
          setDisabled((pre) => !pre);
        })
        .catch(() => {
          setDisabled((pre) => !pre);
        });
    }
  };

  return (
    <Dialog open={open} onClose={() => hideModal(false)}>
      <div className={style.collectModal}>
        <div className={style.collectModaltop}>{t("collect_book")}</div>
        <div className={style.collectModalbody}>
          <div className={style.swichRow}>
            <p className={style.swichRowlabel}>{t("damaged")}</p>
            <Switch
              {...label}
              checked={chargeType.damaged}
              onClick={(e) =>
                onChargeBy({
                  name: "Damaged",
                  value: e.target.checked,
                })
              }
            />
          </div>
          <div className={style.swichRow}>
            <p className={style.swichRowlabel}>{t("lost")}</p>
            <Switch
              {...label}
              checked={chargeType.lost}
              onClick={(e) =>
                onChargeBy({
                  name: "Lost",
                  value: e.target.checked,
                })
              }
            />
          </div>
          {collectData.enterAmount && (
            <p className={style.errorspan}>{t("charg_reason")}</p>
          )}
          {(chargeType.damaged || chargeType.lost) && (
            <>
              <div className={style.inputt}>
                <TextInput
                  type="number"
                  placeholder={t("enter_damage_amount")}
                  label={t("enter_damage_fine")}
                  value={collectData.fineCharge}
                  handleChange={(e) =>
                    setCollectData({
                      ...collectData,
                      fineCharge: parseInt(e.target.value),
                    })
                  }
                  customStyleInput={{
                    width: "100%",
                    padding: "0.6rem",
                  }}
                />
              </div>
              <div className={style.inputt}>
                <h3 className={style.input_label}>{t("collection_type")}</h3>
                <p className={style.input_labelsmall}>{t("cheque_note")}</p>
                <div
                  className={
                    isOpen
                      ? `${style.formselectactive} ${style.formselect}`
                      : style.formselect
                  }
                  onClick={() => setIsOpen(!isOpen)}
                >
                  {collectData.paymentType && collectData.paymentType !== "" ? (
                    <h6
                      className={style.genderh}
                      onClick={() => setIsOpen(true)}
                    >
                      {collectData.paymentType}
                    </h6>
                  ) : (
                    <h6
                      className={`${style.genderplaceholder} ${style.genderh}`}
                      onClick={() => setIsOpen(true)}
                    >
                      {t("select_payment_mode")}
                    </h6>
                  )}

                  {isOpen ? (
                    <img
                      className={`${style.updownIcon} ${style.downIcon}`}
                      src={`${imageShowUrl2}/up-triangle-icon.svg`}
                      alt=""
                    />
                  ) : (
                    <img
                      className={style.upIcon}
                      src={`${imageShowUrl2}/downn-icon.svg`}
                      alt=""
                    />
                  )}
                  <SelectOptions
                    open={isOpen}
                    onClose={() => setIsOpen(false)}
                    setFormData={setCollectData}
                    formData={collectData}
                  />
                </div>
              </div>
            </>
          )}

          <button
            disabled={disabled}
            className={style.confirmbtn}
            onClick={handleCollect}
          >
            {t("confirm")}
          </button>
        </div>
      </div>
      {disabled && <QLoader />}
    </Dialog>
  );
}

export default CollectModal;
